<template>
  <section class="open-wrap">
    <compontLeft :navData="navData" :targetName="targetName"></compontLeft>
    <section class="open-wrap-content">
      <a class="open-group-add" @click="showPop"></a>
      <el-table :data="applyList" border>
        <el-table-column label="" prop="applyName"></el-table-column>
        <el-table-column label="" prop="applyDesc" show-overflow-tooltip></el-table-column>
        <el-table-column label="">
          <template scope="scope">
            <a class="open-list-btn" size="small" @click="showPop(scope.$index)"></a>
            <a class="open-list-btn" @click="deleteApply(scope.row.id)"></a>
            <router-link class="open-list-btn" :to="{name: 'applyApiList', query: {applyId: scope.row.id}}">API</router-link>
            <router-link class="open-list-btn" :to="{name: 'useApiList', query: {applyId: scope.row.id}}">API</router-link>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="pageSize" class="open-page"></el-pagination>
    </section>

    <el-dialog :title="title" :visible.sync="visible">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="open-form">
        <el-form-item label="" prop="applyName" class="open-form-input">
          <el-input v-model="ruleForm.applyName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="applyDesc" class="open-form-input">
          <el-input type="textarea" :row="3" v-model="ruleForm.applyDesc" placeholder=""></el-input>
        </el-form-item>
        <el-form-item class="open-center">
          <el-button class="open-login-btn" @click="visible=false"></el-button>
          <el-button class="open-login-btn" type="primary" @click="submitForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import {getPageData} from '@/assets/js/common.js'
  import compontLeft from '@/components/LEFT.vue'
  import userService from '@/api/userService.js'
  export default{
    name: 'Apply-list',
    data () {
      return {
        constans: constans, // 
        userId: this.$store.state.login.userInfo.id,
        navData: config.navLeft[this.$store.state.login.userInfo.userType],
        targetName: 0, // 
        currentPage: 1,
        pageSize: 10,
        applyList: [],
        total: 0,
        ruleForm: {
          applyName: '',
          applyDesc: ''
        },
        title: '',
        rules: {
          applyName: [{
            required: true,
            message: '',
            trigger: 'blur'
          }, {
            message: '-_*',
            trigger: 'change, blur',
            pattern: config.validate.appName
          }],
          applyDesc: [{
            required: true,
            message: '',
            trigger: 'blur'
          }]
        },
        visible: false,
        index: 0
      }
    },
    created () {
      this.fetch(this.currentPage, this.pageSize)
    },
    methods: {
      //
      fetch (currentPage, pageSize) {
        this.applyList = []
        let userId = this.userId
        userService.getResponseByBody('applyList', currentPage, pageSize, {userId})
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let status = oData.status
            if (status === 'success') {
              this.applyList = oData.data.items
              this.total = oData.data.totalNumber
            } else {
              this.$message.error(message)
            }
          })
          .catch( error => {
            console.log(error)
          })
      },
      handleCurrentChange (pIndex) {
        this.currentPage = pIndex
        this.fetch(this.currentPage, this.pageSize)
      },
      // 
      deleteApply (id) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          userService.getResponse('delApply', {id})
            .then((response) => {
              let oData = response.data
              let message = oData.message
              let status = oData.status
              if (status === 'success') {
                this.fetch(this.currentPage, this.pageSize)
              } else {
                this.$message.error(message)
              }
            })
            .catch(error => {
              this.$message.error(error)
            })
        }).catch()
      },
      //
      showPop (nIndex) {
        this.visible = true
        this.index = nIndex
        let targetData = this.applyList[nIndex]
        if (targetData) { // 
          this.title = ''
          this.ruleForm.applyName = targetData.applyName
          this.ruleForm.applyDesc = targetData.applyDesc
          this.ruleForm.id = targetData.id
        } else { // 
          this.title = ''
          this.ruleForm = {
            applyName: '',
            applyDesc: ''
          }
        }
      },
      submitForm (ruleForm) {
        this.$refs[ruleForm].validate((valid) => {
          if (valid) {
            this.addApply()
          } else {
            this.$message.error('!!');
            return false
          }
        })
      },
      //
      addApply () {
        userService.getResponse('addApply', this.ruleForm)
          .then((response) => {
            let oData = response.data
            let message = oData.message || ''
            let status = oData.status
            if (status === 'success') {
              this.visible = false
              if (this.ruleForm.id) { // 
                this.applyList[this.index].applyName = this.ruleForm.applyName
                this.applyList[this.index].applyDesc = this.ruleForm.applyDesc
                this.$message.success('')
              } else { // 
                this.fetch(this.currentPage, this.pageSize)
              }
            } else {
              this.$message.error(message);
            }
          })
          .catch(error => {
            this.$message.error(error);
          })
      }
    },
    components: { compontLeft }
  }
</script>



