<template>
  <section class="open-login-bg">
    <div class="open-login-info"><span class="open-content-title"></span><br>5<br><span class="goto">{{jumpTime}}</span><router-link :to="{name: 'index'}"></router-link></div>
  </section>
</template>
<style lang="less" scoped>
</style>
<script type='es6'>
  import config from '../assets/js/config.js'
  import md5 from 'js-md5'
  export default{
    name: 'sign-success',
    data(){
      return {
        jumpTime: config.jumpTime
      }
    },
    beforeRouteLeave (to, from, next) {
      clearTimeout(this.time)
      this.jumpTime = 5
      next()
    },
    created () {
      this.time = setInterval(() => {
        this.jumpTime --;
        if (this.jumpTime <= 0) {
          this.$router.push({name: 'index'})
          clearTimeout(this.time)
        }
      }, 1000)
    },
    methods: {
    },
    components: { }
  }
</script>



