<template>
  <section class="open-login-bg">
    <div class="open-login-wrap">
      <div class="open-login-img"></div>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="open-form">
        <h3 class="open-content-title"></h3>
        <el-form-item label="" prop="username" class="open-form-input">
          <el-input v-model="ruleForm.username" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="email" class="open-form-input">
          <el-input type="email" v-model="ruleForm.email" placeholder=""></el-input>
        </el-form-item>
        <!--<el-form-item label="" prop="tel" class="open-form-input">
          <el-input type="tel" v-model="ruleForm.tel" placeholder=""></el-input>
        </el-form-item>-->
        <el-form-item label="" prop="password" class="open-form-input">
          <el-input type="password" v-model="ruleForm.password" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="rePassword" class="open-form-input">
          <el-input type="password" v-model="ruleForm.rePassword" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="userType">
          <el-radio-group v-model="ruleForm.userType" @keyup.enter.native="onEnter(ruleForm)">
            <el-radio class="radio" :label="key" v-for="(item, key) in userType" v-if="key != 0">{{item}}</el-radio>
          </el-radio-group>
          <!--<el-select v-model="ruleForm.userType">
            <el-option :label="item" :value="key" v-if="key != 0" v-for="(item, key) in userType"></el-option>
          </el-select>-->
        </el-form-item>
        <!--<el-form-item label="" prop="mem" class="open-form-input">
          <el-input type="textarea" :rows="2" v-model="ruleForm.mem" placeholder=""></el-input>
        </el-form-item>-->
        <el-form-item class="open-center">
          <el-button class="open-login-btn" type="primary" @click="submitForm('ruleForm')" autofocus></el-button>
        </el-form-item>
        <p class="open-message"><router-link :to="{name: 'login'}"></router-link></p>
      </el-form>
    </div>
  </section>
</template>
<style lang="less" scoped>
</style>
<script type='es6'>
  import config from '../assets/js/config.js'
  import md5 from 'js-md5'
  import userService from '@/api/userService.js'
  export default{
    name: 'sign-up',
    data(){
      let checkName = (rule, value, callback) => {
        userService.getResponse('checkName', {username: value})
          .then(response => {
            let oData = response.data
            let message = oData.message
            let status = oData.status
            if (status != 'success') {
              callback(new Error(message));
            } else {
              callback()
            }
          })
          .catch((error) => {
            callback(new Error(''));
          })
      }
      let validatorEmail = (rule, value, callback) => {
        userService.getResponse('checkEmail', {email: value})
          .then(response => {
            let oData = response.data
            let message = oData.message
            let status = oData.status
            if (status != 'success') {
              callback(new Error(message));
            } else {
              callback()
            }
          })
          .catch((error) => {
            callback(new Error(''));
          })
      }
      let validatorPwd = (ruke, value, callback) => {
        value != this.ruleForm.password ? callback(new Error('')) : callback();
      }
      return {
        userType: config.userType,
        ruleForm: {
          username: '',
          email: '',
          tel: '',
          password: '',
          userType: '',
          rePassword: '',
          mem: ''
        },
        rules: {
          username: [{
            required: true,
            message: '',
            trigger: 'blur',
          }, {
            min: 3,
            max: 20,
            message: '320'
          }, {
            message: '-_*',
            trigger: 'change, blur',
            pattern: config.validate.userName
          }, {
            validator: checkName,
            trigger: 'blur'
          }],
          password: [{
            required: true,
            message: '',
            trigger: 'blur'
          } , {
            min: 6,
            max: 20,
            message: '620'
          }, {
            message: '.-*@?!',
            trigger: 'change, blur',
            pattern: config.validate.passWord
          }],
          rePassword: [{
            required: true,
            message: '',
            trigger: 'blur'
          }, {
            trigger: 'blur',
            validator: validatorPwd
          }],
          email: [{
            required: true,
            message: '',
            trigger: 'blur'
          }, {
            message: '',
            trigger: 'change, blur',
            pattern: config.validate.email
          }, {
            validator: validatorEmail,
            trigger: 'blur'
          }],
          tel: [{
            // required: true,
            message: '',
            trigger: 'blur',
            max: 11
          }, {
            message: '',
            trigger: 'change, blur',
            pattern: config.validate.tel
          }],
          userType: [{
            required: true,
            trigger: 'blur',
            message: ''
          }]
        }
      }
    },
    methods: {
      // 
      submitForm (ruleForm) {
        this.$refs['ruleForm'].validate((valid) => {
          if (valid) {
            this.onRegist()
          } else {
            this.$message.error('!!');
            return false
          }
        })
      },
      // 
      onRegist() {
        // this.ruleForm.password = md5(this.ruleForm.password);
        let ruleForm = {...this.ruleForm}
        ruleForm.password = md5(ruleForm.password);
        delete  ruleForm.rePassword
        userService.getResponse('register', ruleForm).then(response => {
          let oData = response.data
          let sMessage = oData.message;
          let nCode = oData.code;
          if (nCode === '1') {
            // this.$message.success(', ')
             this.$router.push({
              name: 'signSuccess'
            })
          }else{
            this.$message.error(sMessage);
          }
        })
      },
      // enter
      onEnter() {
        this.$nextTick(function() {
          this.submitForm ()
        })
      }
    },
    components: { }
  }
</script>



