import Vue from 'vue'
import Router from 'vue-router'
import header from '@/components/HEAD.vue' // 
import compontCurmbs from '@/components/CURMBS.vue'
import footer from '@/components/FOOTER.vue'
import index from '../views/index.vue' // 
import login from '../views/login.vue' // 
import signUp from '../views/signUp.vue' // 
import signSuccess from '../views/signSuccess.vue' // 
import addApi from '@/views/apiSupply/addApi.vue'
import apiDetail from '@/views/apiSupply/apiDetail.vue'
import groupList from '@/views/apiSupply/groupList.vue'
import apiList from '@/views/apiSupply/apiList.vue'
import apiListCommon from '@/views/apiList.vue'
import applyList from '@/views/developer/applyList.vue'
