import dataAnalysis from '@/views/dataAnalysis.vue'

Vue.use(Router)

let router = new Router({
  // base: '../',
  // mode: 'history',
  suppressTransitionError: process.env.NODE_ENV.trim() === 'production', // 
  routes: [
    {
      path: '*',
      redirect: {
        name: 'index'
      }
    },
    {
      path: '/',
      redirect: {
        name: 'index'
      }
    }, {// 
      path: '/index/',
      name: 'index',
      components: {
        header: header,
        default: index,
        footer: footer
      },
      meta: {
        title: ''
      }
    }, {// 
      path: '/login',
      name: 'login',
      component: login,
      meta: {
        title: ''
      }
    }, {// 
      path: '/signup',
      name: 'signUp',
      component: signUp,
      meta: {
        title: ''
      }
    }, {// 
      path: '/signSuccess',
      name: 'signSuccess',
      component: signSuccess,
      meta: {
        title: ''
      }
    }, {
      path: '/apiList',
      name: 'apiListCommon',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: apiListCommon,
        footer: footer
      },
      meta: {
        title: 'api',
        curmbsData: [{
          title: 'api',
          path: '/apiList'
        }]
      }
    }, {
      path: '/supply/person',
      name: 'supplyPerson',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: person,
        footer: footer
      },
      meta: {
        title: '',
        curmbsData: [{
          title: '',
          path: ''
        }],
        needToLogin: true // 
      }
    }, { // 
      path: '/supply/addApi',
      name: 'addApi',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: addApi,
        footer: footer
      },
      meta: {
        title: '',
        curmbsData: [{
          title: '',
          path: ''
        }, {
          title: '',
          path: '/supply/groupList'
        }, {
          title: 'API',
          path: '/supply/addApi'
        }],
        needToLogin: true // 
      }
    }, { // api
      path: '/supply/apiDetail',
      name: 'apiDetail',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: apiDetail,
        footer: footer
      },
      meta: {
        title: '',
        curmbsData: [{
          title: '',
          path: ''
        }, {
          title: '',
          path: '/supply/groupList'
        }, {
          title: 'API',
          path: '/supply/apiDetail'
        }],
        needToLogin: true // 
      }
    }, { // 
      path: '/supply/groupList',
      name: 'groupList',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: groupList,
        footer: footer
      },
      meta: {
        title: '',
        curmbsData: [{
          title: '',
          path: ''
        }, {
          title: '',
          path: '/supply/groupList'
        }],
        needToLogin: true // 
      }
    }, { // api
      path: '/supply/apiList',
      name: 'apiList',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: apiList,
        footer: footer
      },
      meta: {
        title: 'api',
        curmbsData: [{
          title: '',
          path: ''
        }, {
          title: '',
          path: '/supply/groupList'
        }, {
          title: 'api',
          path: ''
        }],
        needToLogin: true // 
      }
    }, { // api
      path: '/supply/dataAnalysis',
      name: 'apiAnalysis',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: dataAnalysis,
        footer: footer
      },
      meta: {
        title: 'api',
        curmbsData: [{
          title: '',
          path: ''
        }, {
          title: '',
          path: '/supply/groupList'
        }, {
          title: 'API',
          path: ''
        }],
        needToLogin: true // 
      }
    }, {// 
      path: '/develop/person',
      name: 'developPerson',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: person,
        footer: footer
      },
      meta: {
        title: '',
        curmbsData: [{
          title: '',
          path: ''
        }],
        needToLogin: true // 
      }
    }, { // api
      path: '/develop/useMyApi',
      name: 'useMyApi',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: useMyApi,
        footer: footer
      },
      meta: {
        title: '',
        curmbsData: [{
          title: '',
          path: ''
        }, {
          title: '',
          path: ''
        }],
        needToLogin: true // 
      }
    }, {// 
      path: '/develop/applyList',
      name: 'applyList',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: applyList,
        footer: footer
      },
      meta: {
        title: '',
        curmbsData: [{
          title: '',
          path: ''
        }, {
          title: '',
          path: ''
        }],
        needToLogin: true // 
      }
    }, {// api
      path: '/develop/useApiList',
      name: 'useApiList',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: useApiList,
        footer: footer
      },
      meta: {
        title: 'API',
        curmbsData: [{
          title: '',
          path: ''
        }, {
          title: '',
          path: '/develop/applyList'
        }, {
          title: 'API',
          path: ''
        }],
        needToLogin: true // 
      }
    }, {
      path: '/develop/apiList',
      name: 'applyApiList',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: apiListCommon,
        footer: footer
      },
      meta: {
        title: 'api',
        curmbsData: [{
          title: '',
          path: ''
        }, {
          title: '',
          path: '/develop/applyList'
        }, {
          title: 'API',
          path: ''
        }]
      }
    }, { // api
      path: '/develop/apiDetail',
      name: 'developApiDetail',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: apiDetail,
        footer: footer
      },
      meta: {
        title: '',
        curmbsData: [{
          title: '',
          path: ''
        }, {
          title: '',
          path: '/develop/applyList'
        }, {
          title: 'API',
          path: ''
        }],
        needToLogin: true // 
      }
    }, { // api
      path: '/develop/dataAnalysis',
      name: 'dataAnalysis',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: dataAnalysis,
        footer: footer
      },
      meta: {
        title: 'API',
        curmbsData: [{
          title: '',
          path: ''
        }, {
          title: '',
          path: '/develop/useMyApi'
        }, {
          title: 'API',
          path: ''
        }],
        needToLogin: true // 
      }
    }, { // --
      path: '/doc/newGuide',
      name: 'newGuide',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: newGuide,
        footer: footer
      },
      meta: {
        title: '',
        index: 0, // 
        curmbsData: [{
          title: '',
          path: ''
        }, {
          title: '',
          path: ''
        }]
      }
    }, { // --
      path: '/doc/regService',
      name: 'regService',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: newGuide,
        footer: footer
      },
      meta: {
        title: 'API',
        index: 1, // 
        curmbsData: [{
          title: '',
          path: ''
        }, {
          title: 'API',
          path: ''
        }]
      }
    }, { // --API
      path: '/doc/useApi',
      name: 'useApi',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: newGuide,
        footer: footer
      },
      meta: {
        title: 'API',
        index: 2, // 
        curmbsData: [{
          title: '',
          path: ''
        }, {
          title: 'API',
          path: ''
        }]
      }
    }, { // --API
      path: '/doc/definition',
      name: 'definition',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: newGuide,
        footer: footer
      },
      meta: {
        title: '',
        index: 3, // 
        curmbsData: [{
          title: '',
          path: ''
        }, {
          title: '',
          path: ''
        }]
      }
    }, { // --API
      path: '/doc/apiStandard',
      name: 'apiStandard',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: newGuide,
        footer: footer
      },
      meta: {
        title: '',
        index: 4, // 
        curmbsData: [{
          title: '',
          path: ''
        }, {
          title: '',
          path: ''
        }]
      }
    }
  ]
})
import * as types from '@/store/mutation-types.js'
import config from '@/assets/js/config.js'
// 
router.beforeEach((to, from, next) => {
  router.app.curmbsData = to.meta.curmbsData
  let userInfo = localStorage.getItem('session')
  let oUserInfo
  if (userInfo) {
    userInfo = JSON.parse(userInfo)
    let __time = userInfo.time * 1
    oUserInfo = userInfo.userInfo
    let sNowTime = new Date().getTime() * 1
    let expiredTime = config.expiredTime // 
    oUserInfo = sNowTime - __time < expiredTime ? oUserInfo : null
    router.app.$options.store.commit(types.UPDATEUSERINFO, oUserInfo)
  }
  if (!router.app.userInfo && !userInfo && !oUserInfo && to.meta.needToLogin) { // 
    return next({name: 'index'})
  }
  next()
})

export default router



